import Vue from 'vue'

import fillzero from './fillzero'
import date from './date';

// 注册到全局
// Vue.filter('fillzero',fillzero)
// Vue.filter('date',date)
// 注册到全局的另外写法(防止很多过滤器的时候)
let filters = {
    fillzero,date
}
Object.keys(filters).forEach(key=>Vue.filter(key,filters[key]))
// for(let key in obj)对象的枚举 约等于 keys +values
// Object.keys 枚举键 返回由键组成的数组
// console.log(Object.keys({a:'apple',b:'bannana',p:'pear'}))//["a", "b", "p"]


// Object.values 枚举值 返回由值组成的数组 
// console.log(Object.values({a:'apple',b:'banana',p:'pear'}))//["apple", "banana", "pear"]
// Object.assign