    //async await 同步的方式写异步的代码
    async activated(){
        //串行有关联 渲染一次
        let resHome = await axios({
            url:'/api/home',
            params:{_page:1,_limit:10}
        });

        let resBanner = await axios({
            url:'/api/banner',
            params:{_page:1,_limit:3,prevParam:resHome.data.data[0]._id}
        });

        this.banners = resBanner.data.data
        this.list = resHome.data.data;
        
    }
